<template>
  <div>
    <section class="container mb-3" id="news">
      <div class="row">
        <div class="col-md-4 image--about2 img mb-3 mb-md-0"></div>
        <div class="col-md-8">
          <h3 class="mb-5">[ Dalla  ]</h3>
          <p>
            Dalla Goods <br />
            Dalla
            Goods<br /><br />
            <br />
            <br />
            100<br />
            <br /><br />
            
          </p>
        </div>
      </div>
    </section>
    <section class="image--about1 img py-5 mb-4" id="about">
      <div class="container">
        <h3 class="mb-5">[ Dalla  ]</h3>
        <p>
          Dalla
          Goods2020<br /><br />
          <br />
           Art  Emotions  Hand made<br />
          <br /><br />
          <br />
          
        </p>
      </div>
    </section>
    <section class="container mb-4" id="service">
      <h3 class="mb-5">[ Dalla  ]</h3>
      <ul class="row justify-content-between list-unstyled mb-0">
        <li class="card col-md-4 mb-3 mb-md-0 border-0">
          <span class="display-3 mx-auto bg-primary rounded-circle p-4"
            ><i class="fas fa-crown text-light"></i
          ></span>
          <div class="card-body">
            <p class="h4 text-center"></p>
            <p class="card-text">
              
            </p>
          </div>
        </li>
        <li class="card col-md-4 mb-3 mb-md-0 border-0">
          <span class="display-3 mx-auto bg-primary rounded-circle p-4"
            ><i class="fas fa-shuttle-van text-light"></i
          ></span>
          <div class="card-body">
            <p class="h4 text-center"></p>
            <p class="card-text">
              24
            </p>
          </div>
        </li>
        <li class="card col-md-4 mb-3 mb-md-0 border-0">
          <span class="display-3 mx-auto bg-primary rounded-circle p-4"
            ><i class="fas fa-paint-brush text-light"></i
          ></span>
          <div class="card-body">
            <p class="h4 text-center"></p>
            <p class="card-text">
              
            </p>
          </div>
        </li>
      </ul>
    </section>
    <section id="products">
      <h3 class="mb-5 text-center">[ Dalla  ]</h3>
      <ul class="slider list-unstyled">
        <li class="slider__item" v-for="item in products" :key="item.id">
          <div
            class="img h-100"
            :style="{ backgroundImage: `url(${item.imageUrl})` }"
          >
            <div
              class="slider__content h-100 justify-content-center align-items-center p-3"
            >
              <h4 class="mb-3 text-light">{{ item.title }}</h4>
              <button
                type="button"
                class="btn btn-outline-light"
                @click="getDetail(item.id)"
              >
                
              </button>
            </div>
          </div>
        </li>
      </ul>
    </section>
  </div>
</template>
<script>
import $ from 'jquery'
import 'slick-carousel'

export default {
  data () {
    return {
      products: []
    }
  },
  methods: {
    getProducts () {
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products/all`
      vm.$http.get(api).then(res => {
        vm.products = res.data.products.filter(e => e.is_enabled === 1)
      })
    },
    setSlider () {
      $('.slider').slick({
        slidesToShow: 4,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 1000,
        arrows: false,
        responsive: [
          {
            breakpoint: 768,
            settings: {
              slidesToShow: 3
            }
          },
          {
            breakpoint: 576,
            settings: {
              slidesToShow: 2
            }
          }
        ]
      })
    },
    getDetail (id) {
      this.$router.push({ path: `/product_detail/${id}` })
    }
  },
  created () {
    this.getProducts()
  },
  updated () {
    this.setSlider()
  }
}
</script>
